<template>
  <v-container>
    <v-layout row wrap>
      <v-flex xs12 mb6>
        <v-btn color="primary" @click="store_position()">Save</v-btn>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  props: ["results_data", "userinfo", "position_list"],
  data() {
    return {
      name: "test"
    };
  },
  methods: {
    store_position() {
      var this_component = this;
      this.$axios
        .post("/storepositions", {
          email: this.$store.state.userinfo.email,
          position_list: this.$store.state.position_list
        })
        .then(function(response) {
          console.log(response.data);
          var message = response.data["message"];

          if (message == "success") {
            this_component.position_stored = true;
            console.log(message);
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  }
};
</script>

