<template>
  <v-container>
    <!-------------- position list toolbar------------->
    <v-layout v-if="positionlist_view" row wrap>
      <v-toolbar dense>
        <v-toolbar-side-icon @click.stop="portfolio_drawer = !portfolio_drawer"></v-toolbar-side-icon>
        <v-toolbar-title>{{ selected_portfolio_title }}</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon @click="add_new_position()">
          <v-icon>add</v-icon>
        </v-btn>
      </v-toolbar>
    </v-layout>

    <!-------------- position list ------------->
    <v-layout v-if="positionlist_view" row warp>
      <v-flex xs12 sm6 offset-sm3>
        <v-card min-height="200px">
          <v-list two-line>
            <template v-for="(position, index) in position_list">
              <v-list-tile :key="position.name" avatar ripple @click="selecte_position(position)">
                <v-list-tile-content>
                  <v-list-tile-title>{{ position.name }}</v-list-tile-title>
                  <v-list-tile-sub-title class="text--primary">{{ position.address }}</v-list-tile-sub-title>
                  <v-list-tile-sub-title>{{ position.book_value }}</v-list-tile-sub-title>
                </v-list-tile-content>

                <v-list-tile-action>
                  <v-list-tile-action-text>{{ position.position_type }}</v-list-tile-action-text>
                </v-list-tile-action>
              </v-list-tile>
              <v-divider v-if="index + 1 <= position_list.length" :key="index"></v-divider>
            </template>
          </v-list>
        </v-card>
      </v-flex>
    </v-layout>

    <!-------------- nevigation ------------->
    <v-navigation-drawer v-if="positionlist_view" v-model="portfolio_drawer" absolute temporary>
      <v-list class="pa-1">
        <v-list-tile avatar>
          <!-- <v-list-tile-avatar>
            <img src="https://randomuser.me/api/portraits/men/85.jpg">
          </v-list-tile-avatar>-->
          <v-list-tile-content>
            <v-list-tile-title>{{ userinfo }}</v-list-tile-title>
          </v-list-tile-content>
          <v-btn icon>
            <v-icon>add</v-icon>
          </v-btn>
        </v-list-tile>
      </v-list>

      <v-list class="pt-0" dense>
        <v-divider></v-divider>

        <v-list-tile
          v-for="port in portfolio_list"
          :key="port.title"
          @click.stop="position_list = port.position_list; 
                      portfolio_drawer = !portfolio_drawer;
                      selected_portfolio_title = port.title;
                      "
        >
          <v-list-tile-action>
            <v-icon>{{ port.icon }}</v-icon>
          </v-list-tile-action>

          <v-list-tile-content>
            <v-list-tile-title>{{ port.title }}</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>

    <!-------------- position info toolbar------------->
    <v-layout v-if="!positionlist_view" row wrap>
      <v-toolbar dense>
        <v-btn icon @click="positionlist_view = !positionlist_view">
          <v-icon>arrow_back</v-icon>
        </v-btn>
        <v-spacer></v-spacer>
        <v-btn icon>
          <v-icon>delete</v-icon>
        </v-btn>
      </v-toolbar>
    </v-layout>

    <!-------------- position info ------------->
    <v-layout v-if="!positionlist_view" class="pa-2" row wrap>
      <!-- <v-flex xs6>
        <v-text-field label="Key" v-model="access_key" placeholder="access key"></v-text-field>
      </v-flex>
      <v-flex xs6>
        <v-btn color="primary" @click="load_positions_from_key()">Load</v-btn>
      </v-flex> -->
      <v-flex xs12>
        <v-text-field label="Name" v-model="position_info.name"></v-text-field>
      </v-flex>
      <v-flex xs8>
        <v-text-field label="Address" v-model="position_info.address"></v-text-field>
      </v-flex>
      <v-flex xs4>
        <v-layout row justify-center>
          <v-dialog v-model="address_dialog" persistent max-width="600px">
            <template v-slot:activator="{ on }">
              <v-btn color="primary" dark v-on="on">Edit</v-btn>
            </template>
            <v-tabs dark color="cyan" show-arrows>
              <v-tabs-slider color="yellow"></v-tabs-slider>

              <v-tab key="directInputTabKey" href="#directInputTabKey">Direct Input</v-tab>

              <v-tab key="naverInputTabKey" href="#naverInputTabKey">Naver</v-tab>

              <v-tabs-items>
                <v-tab-item key="directInputTabKey" value="directInputTabKey">
                  <v-card>
                    <v-card-title>
                      <span class="headline"></span>
                    </v-card-title>
                    <v-card-text>
                      <v-container grid-list-md>
                        <v-layout wrap>
                          <v-flex xs12 sm12 md12>
                            <v-text-field label="Address" v-model="position_info.address"></v-text-field>
                          </v-flex>

                          <v-flex xs12 sm6 md4>
                            <v-text-field
                              label="Legal Dong"
                              required
                              v-model="position_info.legal_dong"
                            ></v-text-field>
                          </v-flex>
                          <v-flex xs12 sm6 md4>
                            <v-text-field
                              label="Apt Name"
                              v-model="position_info.apt_name"
                              hint="example of helper text only on focus"
                            ></v-text-field>
                          </v-flex>
                          <v-flex xs12 sm6 md4>
                            <v-text-field
                              label="Private Area"
                              required
                              v-model="position_info.private_area"
                            ></v-text-field>
                          </v-flex>
                          <v-flex xs12 sm6 md4>
                            <v-text-field label="Region Code" v-model="position_info.region_code"></v-text-field>
                          </v-flex>
                        </v-layout>
                      </v-container>
                      <small>*indicates required field</small>
                    </v-card-text>
                    <v-card-actions>
                      <v-spacer></v-spacer>
                      <v-btn color="blue darken-1" flat @click="address_dialog = false">Close</v-btn>
                    </v-card-actions>
                  </v-card>
                </v-tab-item>
                <v-tab-item key="naverInputTabKey" value="naverInputTabKey">
                  <v-card>
                    <v-card-title>
                      <span class="headline">Naver</span>
                    </v-card-title>
                    <v-card-actions>
                      <v-spacer></v-spacer>
                      <v-btn color="blue darken-1" flat @click="address_dialog = false">Close</v-btn>
                    </v-card-actions>
                  </v-card>
                </v-tab-item>
              </v-tabs-items>
            </v-tabs>
          </v-dialog>
        </v-layout>
      </v-flex>
      <v-flex
        xs12
      >{{ position_info.legal_dong }} {{ position_info.apt_name }} {{ position_info.private_area }} {{ position_info.region_code }}</v-flex>
      <v-flex xs6>
        <v-text-field label="BookValue" v-model="position_info.book_value"></v-text-field>
      </v-flex>
      <v-flex xs6>
        <v-text-field label="BookDate" type="date" v-model="position_info.book_date"></v-text-field>
      </v-flex>
      <v-flex xs12 sm6 class="py-2">
        <v-btn-toggle v-model="position_info.position_type" mandatory>
          <v-btn flat value="owner_occupied">Owner</v-btn>
          <v-btn flat value="rent">Rent</v-btn>
        </v-btn-toggle>
      </v-flex>

      <v-flex v-if="position_info.position_type == 'rent'" xs12 mb6>
        <v-text-field label="Deposit" v-model="position_info.rent.deposit"></v-text-field>
        <v-text-field label="MontlyPayment" v-model="position_info.rent.monthly_payment"></v-text-field>
        <v-text-field label="EffectiveDate" type="date" v-model="position_info.rent.effective_date"></v-text-field>
        <v-text-field label="MaturityDate" type="date" v-model="position_info.rent.maturity_date"></v-text-field>
      </v-flex>

      <v-flex xs6>
        <v-checkbox
          v-model="position_info.hasLoan"
          :label="`Loan : ${position_info.hasLoan.toString()}`"
        ></v-checkbox>
      </v-flex>

      <v-flex xs12 v-if="position_info.hasLoan">
        <v-text-field label="Amount" v-model="position_info.loan.amount"></v-text-field>
        <v-text-field label="Rate" v-model="position_info.loan.rate"></v-text-field>
        <v-text-field label="Type" v-model="position_info.loan.type"></v-text-field>
        <v-btn-toggle v-model="position_info.loan.type" mandatory>
          <v-btn flat value="fixed">Fixed</v-btn>
          <v-btn flat value="floating">Floating</v-btn>
        </v-btn-toggle>
        <v-text-field label="EffectiveDate" type="date" v-model="position_info.rent.effective_date"></v-text-field>
        <v-text-field label="MaturityDate" type="date" v-model="position_info.rent.maturity_date"></v-text-field>
      </v-flex>

      <!-- <v-flex xs12>
        <v-btn color="primary" @click="add_new_position()">Add</v-btn>
        <v-btn color="primary" @click="edit_position()">Edit</v-btn>
        <v-btn color="primary" @click="clear_position()">Clear</v-btn>
      </v-flex> -->
    </v-layout>
    
  </v-container>
</template>

<script>
export default {
  props: ["userinfo", "portfolio_list"],
  data() {
    return {
      positionlist_view: true,
      portfolio_drawer: false,
      address_dialog: false,
      access_key: "",
      
      // portfolio_list: [
      //   { title: "Home", position_list: [] },
      //   { title: "About", position_list: [] }
      // ],
      position_info: {
        name: "test name2",
        asset_type: "apartment",
        address: "test address",
        legal_dong: "test dong",
        apt_name: "test apt",
        private_area: 55.12,
        region_code: 11110,
        book_value: 10000,
        book_date: "2018-10-11",
        //position_type: 0, // 0 : owner_occupied, // 1: rent
        position_type: "owner_occupied", // 1: rent
        rent: {
          deposit: 0,
          monthly_payment: 1000,
          effective_date: "2018-10-11",
          maturity_date: "2018-10-11"
        },
        hasLoan: false,
        loan: {
          amount: 0,
          type: "fixed", // floating
          rate: 0.03,
          effective_date: "2018-10-11",
          maturity_date: "2018-10-11"
        }
      } // positionInfo end
    };
  },
  methods: {
    position_info_valiation: function() {
      var name = this.position_info.name;
      var name_exist = this.$store.state.selected_portfolio.position_list.some(function(item) {
        return item.name == name;
      });

      if (name_exist > 0) {
        console.log("name duplication : " + this.position_info.name);
        return false;
      }

      return true;
    },
    load_positions_from_key: function() {
      var this_component = this;
      this.$axios
        .post("http://127.0.0.1:5000/getpositions", {
          key: this.access_key
        })
        .then(function(response) {
          console.log(response.data);
          var message = response.data["message"];

          if (message == "success") {
            this_component.userinfo = response.data["data"]["userinfo"];
            this_component.position_list =
              response.data["data"]["position_list"];
            this_component.results_data = response.data["data"]["results_data"];
            this_component.isCalculated = false;

            console.log(message);
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    add_new_position: function() {
      //if (!this.position_info_valiation()) return;

      console.log("add start ---------------------------------------");

      var new_position_info = JSON.parse(JSON.stringify(this.$store.state.new_position_info_template));
      this.position_info = new_position_info;
      
      var i = 0;
      var roopName = new_position_info.name;
      do {
        i += 1;
        // roopName = roopName + i.toString();
        // console.log(i);
      } while (this.position_list.some(function(item) {return item.name == roopName + i.toString();}));

      this.position_info.name = roopName + i.toString();

      this.$store.state.selected_portfolio.position_list.push(new_position_info);
      this.$store.state.isCalculated = false;
      console.log("add complete ------------------------------------");
    },
    selecte_position: function(position) {
      this.positionlist_view = false;
      this.position_info = JSON.parse(JSON.stringify(position));
    },
    edit_position: function() {
      this.$store.state.isCalculated = false;
      console.log("edit complete");
    },
    clear_position: function() {
      this.$store.state.isCalculated = false;
      this.$store.state.selected_portfolio.position_list = [];
      console.log("clear complete");
    }
  },
  computed: {
    position_list: {
      get: function() {
        return this.$store.state.selected_portfolio.position_list;
      },
      set: function(p) {
        this.$store.state.selected_portfolio.position_list = p;
      }
    },
    selected_portfolio_title: {
      get: function() {
        return this.$store.state.selected_portfolio.title;
      },
      set: function(p) {
        this.$store.state.selected_portfolio.title = p;
      }
    }
    
    // position_list() {
    //     return this.$store.state.position_list;
    // },
  }
};
</script>
